<template>
	<div class="container">
		<el-upload class="upload-demo"
			drag
			ref="upload"
			action=""
			:auto-upload="false"
			:accept="accept"
			:on-preview="handlePreview"
			:on-remove="handleRemove"
			:before-remove="beforeRemove"
			:on-change="handleChange"
		>
			<i class="el-icon-upload"></i>
			<div class="el-upload__text">geojson<em></em></div>
			<div class="el-upload__tip" slot="tip">.json.geojson</div>
		</el-upload>
  </div>
</template>

<script>
export default {
	name: "UPLOADERGEO",
	data () {
		return {
			accept: ".json,.geojson"
		}
	},
	methods: {
		handleRemove(file, fileList) {
			return this.fileReaderHandle(file.raw).then(fileJson => {
				this.$emit("removeFile", fileJson, file, fileList);
			}).catch(error => {
				console.log(error.message);
			})
			
		},
		handlePreview(file) {
			return this.fileReaderHandle(file.raw).then(fileJson => {
				this.$emit("filePreview", fileJson)
			}).catch(error => {
				this.$notify.error({
					title: '',
					message: error.message
				});
			})
		},
		beforeRemove(file, fileList) {
			return this.$confirm(` ${ file.name }`);
		},
		// 
		handleChange (file, fileList) {
			return this.fileReaderHandle(file.raw).then(fileJson => {
				this.$emit("addFile", fileJson, fileList);
			}).catch(error => {
				this.$notify.error({
					title: '',
					message: error.message
				});
			})
		},

		fileReaderHandle(fileRaw) {
			return new Promise((resolve, reject) => {
				const reader = new FileReader(); // FileReader
				reader.readAsText(fileRaw, "UTF-8"); //  
				reader.onload = (evt) => { //
					const fileString = evt.target.result; // 
					try{
						resolve(JSON.parse(fileString));
					}catch(e){
						reject(e);
					}
					
				}
			});
		}

	}
}
</script>

<style lang="less" scoped>
.upload-demo /deep/ .el-upload-list__item-name{
	cursor: pointer;
}
</style>